@import url('https://fonts.googleapis.com/css?family=Roboto');
@font-face {
  font-family: 'sensa';
  src: url('sensa.woff2') format('woff2'),
       url('sensa.woff') format('woff');
}
@font-face {
  font-family: 'sophia';
  src: url('Sophia.woff2') format('woff2'),
       url('Sophia.woff') format('woff');
}

body {
  margin: 0;
}

.App {
  position: relative;
  height: 100vh;
  text-align: center;
  padding-top: 15%;
}

.App:before {
  background: url('https://images.unsplash.com/photo-1444394668777-6fa6de8b8356?') center / cover;
  content: '';
  position: absolute;
  left: 0; right: 0; top: 0; bottom: 0;
  z-index: -1;
  opacity: 0.3;
}

.hashtag {
  background: -o-linear-gradient(120deg, rgba(147, 112, 219, .9), rgba(255, 197, 208, .9));
  background: -webkit-linear-gradient(330deg, rgba(147, 112, 219, .9), rgba(255, 197, 208, .9));
  background: -o-linear-gradient(330deg, rgba(147, 112, 219, .9), rgba(255, 197, 208, .9));
  background: linear-gradient(120deg, rgba(147, 112, 219, .9), rgba(255, 197, 208, .9));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 15rem;
  font-family: 'sophia';
}

.d-inline {
  display: inline;
  margin: .5rem;
}

.clock {
  font-size: 2.4rem;
  font-family: 'Roboto', sans-serif;
  color: #9370DB;
}



